.flex-wrapper {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.flex-wrapper > #cb-wrapper {
  width: 50%;
  max-width: 400px;
}
.flex-wrapper > #cb-wrapper > #coffee-bk {
  display: block;
  width: 100%;
  height: auto;
  margin: auto;
}
.flex-wrapper > #bt-wrapper > #start-bt {
  font-family: "JetBrains Mono", monospace;
  color: #FEE1CA;
  font-size: 4vw;
  font-feature-settings: none;
  font-variant-ligatures: none;
  background-color: #987961;
  font-weight: bolder;
  border: 0.5vw solid #AC896E;
  border-radius: 20vw;
  padding: 20px 80px;
  max-width: 240px;
}

